import React from "react"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import {graphql, Link, StaticQuery} from "gatsby";
import {Breadcrumb, Card} from "react-bootstrap";

import "./forex.style.scss";


export const Item = ({data}) => (
  <div className="row forex__item">
    <div className="col-md-6">
      <div className="table-responsive">
        <table className="table table-hover">
          <thead>
          <tr className="white_border">
            <td align="center" width="157px">sàn giao dịch</td>
            <td align="center" width="139px" style={{whiteSpace: 'nowrap'}}>MỞ TÀI KHOẢN REAL</td>
            <td align="center" width="132px">Khuyến mãi</td>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td style={{padding: 5, background: '#fff', borderTop: 'none'}}/>
          </tr>
          <tr>
            <td align="center" valign="top"><Link to={data.path}
                                                  title={data.title}>
              <div className="forex__item__img"><img
                src={data.avatar.localFile.publicURL}
                width="141px" height="64px" alt={data.name}/></div>
              <span className="forex__item__name"><h2>{data.name}</h2> </span> </Link>
            </td>
            <td align="center" valign="top">
              <div style={{width: '100px', marginTop: '30px'}}>
                <button className="btn btn-danger btn-sm forex__item__motk"
                        dangerouslySetInnerHTML={{__html: data.link_register}}/>
              </div>
            </td>
            <td align="center" valign="top"><a className="ttkm_vn" href="blog//.html"/></td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div className="col-md-6">
      <div className="table-responsive forex__item-detail">
        <div className="forex__item-detail__title">thông tin chi tiết</div>
        <table className="table table-hover">
          <tbody>
          <tr>
            <td style={{padding: 5, background: '#fff', borderTop: 'none'}}/>
          </tr>
          <tr>
            <td>
              <ul style={{width: '120px', whiteSpace: 'initial'}}>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Download: ${data.download}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Website: ${data.website}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Xếp hạng FPA: ${data.fpa_rate}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Quản lý bởi: ${data.manage_by}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Loại hình: ${data.type}`}}/></li>
              </ul>
            </td>
            <td>
              <ul style={{width: '120px', whiteSpace: 'initial'}}>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Spread: ${data.spread}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Thành lập: ${data.year}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Tài khoản tối thiểu: ${data.account_min}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Khối lượng tối thiểu: ${data.weight_min}`}}/></li>
              </ul>
            </td>
            <td>
              <ul slyle="white-space: initial;">
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Tài khoản dạng: ${data.account_type}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Đòn bẩy tối đa: ${data.lever_max}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Hedging: ${data.hedging.label}`}}/></li>
                <li><span className="icon_forex" dangerouslySetInnerHTML={{ __html: `Nạp tiền: ${data.topup}`}}/></li>
              </ul>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
)

const Page = ({data}) => {

  const renderItem = ({id, path, title, metadata}) => (
    <Item key={id} data={{...metadata, path, title}}/>
  )

  return (
    <Layout>
      <SEO title="Forex broker"/>
      <div className="container non-padding ">
        <div className="row" style={{paddingTop: "10px", marginBottom: 10}}>
          <div className="col-md-12">
            <Card style={{width: "100%"}}>
              <Card.Header style={{padding: 0}}>
                <Breadcrumb
                  listProps={{
                    style: {
                      marginBottom: 0,
                      backgroundColor: "initial",
                      display: "flex",
                      alignItems: "center",
                    },
                  }}
                >
                  <Breadcrumb.Item
                    as={() => (
                      <Link
                        to="/"
                        style={{
                          fontWeight: "bold",
                          fontSize: 14,
                          color: "#333",
                        }}
                      >
                        Home
                      </Link>
                    )}
                  />
                  <span style={{paddingLeft: 10, paddingRight: 10}}>»</span>
                  <Breadcrumb.Item
                    as={() => (
                      <Link
                        to={"#"}
                        style={{
                          fontWeight: "bold",
                          fontSize: 14,
                          color: "#333",
                        }}
                      >
                        Forex Broker
                      </Link>
                    )}
                  />
                </Breadcrumb>
              </Card.Header>
              <Card.Body style={{ padding: 10 }}>
                {data.allWordpressWpForexBroker.nodes.map(renderItem)}
              </Card.Body>
            </Card>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default props => (
  <StaticQuery
    query={graphql`
      query {
        allWordpressWpForexBroker {
          nodes {
            metadata {
              description
              name
              year
              account_min
              weight_min
              lever_max
              type
              account_type
              promo_type
              rebate_gold
              rebate_money
              manage_by
              download
              topup
              website
              fpa_rate
              spread
              link_register
              code_show_fpa_rate
              hedging {
                label
              }
              avatar {
                localFile {
                  publicURL
                }
              }
            }
            id
            title
            path
          }
        }
      }
    `}
    render={data => <Page data={data} {...props} />}
  />
)
